const App = () => {

  const childRef = useRef();
  let location = useLocation();

  useEffect(() => {
    document.body.classList.add('is-loaded')
    childRef.current.init();
    // trackPage(page);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location]);

  return (
    <ScrollReveal
      ref={childRef}
      children={() => (
        <Switch>
          {/* <AppRoute exact path="/watchdogs_s02" component={Home} layout={LayoutDefault} /> */}
          <AppRoute exact path="/watchdogs2" component={Home} layout={LayoutDefault} />
          <AppRoute exact path="/watchdogs2/v1/product" component={V1} layout={LayoutDefault} />

        </Switch>
      )} />
  );
}

export default App;